        this.age = ", " + (parseInt(new Date().getFullYear()) - parseInt(this.user.birthday.substring(0, 4))).toString()
      } else {
        this.age = ""
      }
    },
    // upload(file) {
    //   const axios = require('axios')
    //   const formData = new FormData()
    //   formData.append('smfile', file.file)
    //   axios.post('https://sm.ms/api/v2/upload', formData, {
    //     headers: {
    //       'Content-Type': 'multipart/form-data',
    //       'Authorization': 'JIBr3GZGQFoW27TSgtDfdHVkToQUEw2Q',
    //       'Referer': 'https://sm.ms'
    //     }
    //   }).then(res => {
    //     console.log(res)
    //   })
    // },
    handleChange() {
      console.log(this.fileList);
    },
    handleResumeUploadSuccess(res, file) {
      console.log(res)
      console.log(file)
      this.resumeUrl = res;
      console.log(this.resumeUrl)
    },
    beforeResumeUpload(file) {
      console.log(file)
      var posix = file.name.split(".")[file.name.split(".").length - 1];
      if (posix !== "jpg" && posix !== "png" && posix !== "jpeg") {
        this.$notify({
          type: 'error',
          title: '文件后缀不符合要求',
          message: '请上传jpg/png/jpeg格式的图片文件',
          duration: 4000,
        });
        return false;
      }
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isLt2M) {
        this.$notify({
          type: 'danger',
          title: '文件过大',
          message: '上传头像图片大小不能超过5MB!',
          duration: 4000,
        });
        return false;
      }
      return true;
    },
    handleRemove(file) {
      console.log(file);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    }
  },
  created() {
    var id = this.$route.params.id;
    console.log(id);
    getUserById(id).then((res) => {
      console.log(res);
      if (res.status === 200) {
        this.user = res.data.user;
        console.log(this.user);
        console.log(new Date().getFullYear());
        console.log(parseInt(this.user.birthday.substring(0, 4)))
        var nativePlaceList = this.user.nativePlace.split("$")
        this.multipleFuturePlace = this.user.futurePlace.split("$")
        if (nativePlaceList[0] === nativePlaceList[1]) {
          this.hometown = nativePlaceList[0]
        } else {
          this.hometown = nativePlaceList.join(', ')
        }
        if (this.user.modifyTimes < 1) {
          this.hasModifyTime = false;
        }
        if (this.user.school === "四平校区" || this.user.school === "嘉定校区" || this.user.school === "沪北校区" || this.user.school === "沪西校区") {
          this.isTongji = true;
        } else {
          this.isTongji = false;
        }
        // this.hopeOtherList = this.user.hopeOther.split("$");
        this.calculateAge()
      } else {
        this.$notify({
          type: 'danger',
          title: '遇到错误',
          message: '获取用户信息时遇到了错误',
          duration: 4000,
        });
      }
    });
    // try {
    //     const ws = new WebSocket("ws://localhost:3000/");
    //     ws.onmessage = ({data}) => {
    //         this.message = data;
    //     }
    // } catch (err) {
