<script>

import {getUserBaseInfo, getUserById, updateUser} from "@/api/user";
import swal from "sweetalert2";
import citySelect from "@/views/Components/citySelect";
import {Select, Option, FormItem, Form} from 'element-ui'
import {userRegister} from "@/api/auth";

export default {
  name: 'registerApply',
  components: {
    citySelect,
    [Select.name]: Select,
    [Option.name]: Option,
    [FormItem.name]: FormItem,
  },
  data() {
    return {
      userRegisterForm: {
        "id": "",
        "wechat": "",
        "nativePlace": "",
        "height": "",
        "weight": "",
        "birthday": "",
        "school": "",
        "acceptDiffSchool": "",
        "major": "",
        "futurePlace": "",
        "hopeOther": "",
        "declaration": "",
        "resumeLink": "",
        "avatarLink": "",
        "password": "",
      },
      rePassword: "",
      isTongjiStu: true,
      hasFuturePlace: true,
      weightString: "",
      heightString: "",
      chooseProvince: "",
      chooseCity: "",
      multipleFuturePlace: [],
      hopeOtherList: [
        "",
        "",
        "",
      ],
      futurePlaceOptions: [
        {label: "上海市", value: "上海市"},
        {label: "广东省", value: "广东省"},
        {label: "江苏省", value: "江苏省"},
        {label: "浙江省", value: "浙江省"},
        {label: "北京市", value: "北京市"},
        {label: "四川省", value: "四川省"},
        {label: "湖北省", value: "湖北省"},
        {label: "湖南省", value: "湖南省"},
        {label: "天津市", value: "天津市"},
        {label: "重庆市", value: "重庆市"},
        {label: "安徽省", value: "安徽省"},
        {label: "福建省", value: "福建省"},
        {label: "江西省", value: "江西省"},
        {label: "山东省", value: "山东省"},
        {label: "河南省", value: "河南省"},
        {label: "广西壮族自治区", value: "广西壮族自治区"},
        {label: "海南省", value: "海南省"},
        {label: "河北省", value: "河北省"},
        {label: "山西省", value: "山西省"},
        {label: "内蒙古自治区", value: "内蒙古自治区"},
        {label: "辽宁省", value: "辽宁省"},
        {label: "吉林省", value: "吉林省"},
        {label: "黑龙江省", value: "黑龙江省"},
        {label: "贵州省", value: "贵州省"},
        {label: "云南省", value: "云南省"},
        {label: "西藏自治区", value: "西藏自治区"},
        {label: "陕西省", value: "陕西省"},
        {label: "甘肃省", value: "甘肃省"},
        {label: "青海省", value: "青海省"},
        {label: "宁夏回族自治区", value: "宁夏回族自治区"},
        {label: "新疆维吾尔自治区", value: "新疆维吾尔自治区"},
        {label: "台湾省", value: "台湾省"},
        {label: "香港特别行政区", value: "香港特别行政区"},
        {label: "澳门特别行政区", value: "澳门特别行政区"}
      ],
    }
  },
  methods: {
    onSubmit() {
      // this will be called only after form is valid. You can do an api call here to register users
      this.userRegisterForm.id = this.$route.params.id;
      this.userRegisterForm.futurePlace = this.multipleFuturePlace.join("$");
      this.userRegisterForm.nativePlace = [this.chooseProvince, this.chooseCity].join("$");
      // this.userRegisterForm.weight = parseFloat(this.weightString)
      // this.userRegisterForm.height = parseFloat(this.heightString)
      this.userRegisterForm.weight = this.weightString
      this.userRegisterForm.height = this.heightString
      console.log(this.userRegisterForm);
      console.log(JSON.stringify(this.userRegisterForm))
      if (this.rePassword !== this.userRegisterForm.password) {
        this.$notify({
          type: 'danger',
          title: '两次密码不匹配',
          message: '请检查密码，如有问题请联系群内管理员',
          duration: 4000,
        });
        return
      }
      userRegister(this.userRegisterForm).then((res) => {
        console.log(res);
        if (res.status === 200) {
          swal.fire({
            title: `注册成功`,
            text: '跳转到登录页',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-warning',
            icon: 'success'
          });
          this.$router.push({path: '/userLogin'});
        } else {
          swal.fire({
            title: `遇到错误`,
            text: '注册失败，请联系管理员',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-danger',
            icon: 'error'
          });
        }
      });
    },
    printForm() {
      console.log(this.chooseProvince);
      console.log(this.chooseCity);
      console.log(this.multipleFuturePlace);
      console.log(this.userRegisterForm);
    },
    saveTemp() {
      this.$notify({
        type: 'success',
        title: '暂存成功',
        message: '信息已暂时保存，请尽快完成填写',
        duration: 4000,
      });
    },
    handleCityChoose: function (e) {
      [this.chooseProvince, this.chooseCity] = e;
    }
  },
  created() {
    var id = this.$route.params.id;
    console.log(id);
    getUserBaseInfo({"id": id}).then((res) => {
      console.log(res);
      if (res.data.code === 200) {
        if (res.data.accountStatus === 1) {
          swal.fire({
            title: `不是有效用户`,
            text: '您的账号还未通过审核，或者您还没报名，请等待管理员审核',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-warning',
            icon: 'warning'
          });
          this.$router.push({path: '/registerApplySuccess'});
        } else if (res.data.accountStatus === 1) {
          swal.fire({
            title: `您已经注册过账号了`,
            text: '请直接登录',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-warning',
            icon: 'warning'
          });
          this.$router.push({path: '/userLogin'});
        }
      } else {
        swal.fire({
          title: `遇到错误`,
          text: '用户不存在！',
          buttonsStyling: false,
          confirmButtonClass: 'btn btn-danger',
          icon: 'error'
        });
        this.$router.push({path: '/home'});
      }
    });
  }
};
