<template>
  <div>
    <!-- Header -->
    <div class="header bg-gradient-success py-7 py-lg-8 pt-lg-9">
      <b-container>
        <div class="header-body text-center mb-7">
          <b-row class="justify-content-center">
            <b-col xl="5" lg="6" md="8" class="px-5">
              <!--              <h1 class="text-white">用户登录</h1>-->
              <!--              <p class="text-lead text-white">Use these awesome forms to login or create new account in your project for-->
              <!--                free.</p>-->
            </b-col>
          </b-row>
        </div>
      </b-container>
      <div class="separator separator-bottom separator-skew zindex-100">
        <svg x="0" y="0" viewBox="0 0 2560 100" preserveAspectRatio="none" version="1.1"
             xmlns="http://www.w3.org/2000/svg">
          <polygon class="fill-default" points="2560 0 2560 100 0 100"></polygon>
        </svg>
      </div>
    </div>
    <!-- Page content -->
    <b-container class="mt--8 pb-5">
      <b-row class="justify-content-center">
        <b-col lg="5" md="7">
          <b-card no-body class="bg-secondary border-0 mb-0">
            <b-card-body class="px-lg-5 py-lg-5">
              <div class="text-center text-muted mb-4">
                <h1 class="text-black">用户登录</h1>
              </div>
              <validation-observer v-slot="{handleSubmit}" ref="formValidator">
                <b-form role="form" @submit.prevent="handleSubmit(onSubmit)">
                  <base-input alternative
                              class="mb-3"
                              name="Email"
                              :rules="{required: true, email: true}"
                              prepend-icon="ni ni-email-83"
                              placeholder="你的邮箱"
                              v-model="userLoginForm.email">
                  </base-input>

                  <base-input alternative
                              class="mb-3"
                              name="Password"
                              :rules="{required: true, min: 6}"
                              prepend-icon="ni ni-lock-circle-open"
                              type="password"
                              placeholder="你的密码"
                              v-model="userLoginForm.password">
                  </base-input>
                  <b-form-checkbox v-model="rememberMe">Remember me</b-form-checkbox>
                  <div class="text-center">
                    <base-button type="primary" native-type="submit" class="my-4">登录</base-button>
                  </div>
                </b-form>
              </validation-observer>
            </b-card-body>
          </b-card>
          <b-row class="mt-3">
            <b-col cols="6">
              <!--              <router-link to="/dashboard" class="text-light"><small>&nbsp;&nbsp;&nbsp;忘记密码</small></router-link>-->
            </b-col>
            <b-col cols="6" class="text-right">
              <router-link to="/registerApply" class="text-light"><small>还未报名？&nbsp;&nbsp;&nbsp;</small></router-link>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>
import {userLogin} from "@/api/auth";

export default {
  data() {
    return {
      userLoginForm: {
        email: '',
        password: '',
      },
      rememberMe: false,
    };
  },
  methods: {
    onSubmit() {
      console.log(this.userLoginForm)
      // this will be called only after form is valid. You can do api call here to login
      userLogin(this.userLoginForm).then((res) => {
        if (res.status === 200) {
          this.$store.commit('setToken', res.data);
          console.log(res.data)
          localStorage._id = res.data._id;
          console.log(res.data.expire);
          localStorage.token = res.data.token;
          console.log(res.data.token);
          this.$notify({
            type: 'success',
            title: '登陆成功！',
            message: '欢迎登录嘉人有约线上系统',
            duration: 4000,
          });
          this.$store.commit('isLoginTrue');
          this.$router.push({path: '/user/' + res.data._id});
        } else {
          this.$notify({
            type: 'danger',
            title: '邮箱或密码错误',
            message: '请检查填写的表单是否正确，如有问题请联系群内管理员',
            duration: 4000,
          });
        }
      }).catch(error => {
        console.log(error)
        this.$notify({
          type: 'danger',
          title: '邮箱或密码错误',
          message: '请检查填写的表单是否正确，如有问题请联系群内管理员',
          duration: 4000,
        });
      });
    },
  }
};
</script>
